<template>
  <el-row class="user-info" type='flex' justify='center'>
    <el-col  :xs='18' :sm='12' :md='12'>
      <h1></h1>
      <el-form
        :model='me'
        :rules='rules'>
        <section>
          <h3></h3>
          <p>{{ me.name }}</p>
          <p>{{ me.roleName}}</p>
          <p v-if="me.company">{{ me.company.name}}</p>
          <p v-if="me.soItem"> {{ me.soItem.number}}</p>
        </section>
        <h3></h3>
        <section>
          <el-form-item label="" prop="phone">
            <el-input
              :value="me.phone"
              @change="value => this.updateMe({ phone: value })"
              :disabled="!isShow()"
              placeholder="">
            </el-input>
          </el-form-item>
          <el-form-item label="email" prop='email'>
            <el-input
              :value="me.email"
              @change="value => this.updateMe({ email: value })"
              :disabled="!isShow()"
              placeholder=" email">
            </el-input>
          </el-form-item>
        </section>
        <section>
          <h3></h3>
          <el-form-item label="">
            <el-checkbox @change='showPassword()' style='float:right;'></el-checkbox>
            <el-input
              :value="me.password"
              :type='password'
              @change="value => this.updateMe({ password: value })"
              :disabled="!isShow()"
              placeholder="">
            </el-input>
          </el-form-item>
        </section>
        <div class="button-container">
          <el-button type="primary" @click="submit">
            
          </el-button>
        </div>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
// @ is an alias to /src
import ToPathMixin from '@/mixins/ToPath'

export default {
  name: 'UserInfo',
  mixins: [ToPathMixin],
  data() {
    return {
      password: 'password',
      rules: {
        phone: [
          {
            required: true,
            pattern: /^[0][9]\d{8}$/,
            message: ' : 09xxxxxxxx',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: ' email', trigger: 'blur' },
          {
            type: 'email',
            message: ' example@chuen.com.tw',
            trigger: ['blur', 'change']
          }
        ]
      }
    }
  },
  computed: {
    me() {
      return this.$store.getters.me
    }
  },
  methods: {
    updateMe(payload) {
      this.$store.commit('updateMe', payload)
    },
    submit() {
      this.$store
        .dispatch('updateMe')
        .then(() => {
          this.$message({ message: '', type: 'success' })
          this.toPath('ProjectList')
        })
        .catch(e => {
          this.$message.error(` ${e.response.data.result}`)
        })
    },
    isShow() {
      return this.$store.getters.myPermissions.includes('account:updateSelf')
    },
    showPassword() {
      this.password === 'password'
        ? (this.password = 'text')
        : (this.password = 'password')
    }
  }
}
</script>
