<template>
  <el-row class="entry" type='flex' justify='center'>
    <el-col :md='8' :sm='10' :span='12'>
      <el-form>
        <el-form-item label="">
          <el-input
            type="text"
            name="account"
            v-model="account"
            placeholder="" @input="removeError">
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="password"
            name="password"
            v-model="password"
            placeholder="" @input="removeError">
          </el-input>
        </el-form-item>
        <el-form-item>
          <p class="feedback">
            {{ feedback }}
          </p>
          <div class="button-container">
            <el-button
              type="primary"
              native-type="submit"
              style='float:right;'
              @click.prevent="submit">
              
            </el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Entry',
  data() {
    return {
      account: null,
      password: null,
      feedback: ''
    }
  },
  methods: {
    submit() {
      if (!this.account) return (this.feedback = '')
      if (!this.password) return (this.feedback = '')
      this.$store
        .dispatch('login', {
          account: this.account,
          password: this.password
        })
        .then(() => {
          this.$message({
            message: `${this.account} `,
            type: 'success',
            center: true,
            duration: 1800
          })
          this.$router.push({ name: 'ProjectList' })
        })
        .catch(() => {
          this.feedback = ''
        })
    },
    removeError() {
      this.feedback = ''
    }
  }
}
</script>

<style>
.feedback {
  position: absolute;
}
</style>
