<template lang="html">
<div class="measuresSo">
  <h1></h1>
  <el-form label-width="100px" label-position="left">
    <section>
      <h2></h2>
      <p> {{projectNumber}}</p>
      <p> {{projectName}}</p>
    </section>
    <section>
      <h2></h2>
      <el-form-item label="" required>
        <el-select v-model="projectPhaseId" placeholder="">
         <el-option
           v-for="projectPhase in projectPhases"
           :key="projectPhase.id"
           :label="projectPhase.name"
           :value="projectPhase.id">
         </el-option>
       </el-select>
     </el-form-item>
      <el-form-item label="" required>
        <el-select v-model="soLocationNumber" placeholder="SO-01" @change="onChangeLocation">
         <el-option
           v-for="location in project.soLocation"
           :key="location.number"
           :label="location.number"
           :value="location.number">
         </el-option>
       </el-select>
     </el-form-item>
      <el-form-item label="WISE IP" required>
        <el-input  v-model="wiseIP">
        </el-input>
      </el-form-item>
      <span> {{currentDepth}} </span>
      <el-form-item label="  1m " label-width="300px">
      </el-form-item>
    <el-button @click="measures" :disabled="measuresSoDatas.length === currentDepth || !projectPhaseId || isMeasuring">
      <span v-if="isMeasuring"></span>
      <span v-else></span>
    </el-button>
    </section>
    <section v-if="measuresSoDatas.length">
      <p> {{currentDepth}} {{measuresSoDatas.length}}</p>
      <el-table
        :data="measuresSoDatas"
        style="width: 100%">
        <el-table-column
          fixed
          type="index"
          width="50">
        </el-table-column>
        <el-table-column
          prop="date"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="time"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="depth"
          label="(m)"
          width="80">
        </el-table-column>
        <el-table-column
          prop="temp"
          label="(c)"
          width="150">
        </el-table-column>
        <el-table-column
          prop="VoltageX"
          label=""
          width="120">
        </el-table-column>
        <el-table-column
          prop="degreeX"
          label="(C)"
          width="150">
        </el-table-column>
        <el-table-column
          prop="displacement"
          label="(cm)"
          width="150">
        </el-table-column>
        <el-table-column
          prop="totalDisplacement"
          label="(cm)"
          width="150">
        </el-table-column>
      </el-table>
      <el-button @click="clearMeasuresDatas"></el-button>
      <el-button @click="uploadMeasuresDatas" :disabled="measuresSoDatas.length < currentDepth || isSend" >
        <span v-if="isSend"></span>
        <span v-else></span>
      </el-button>
    </section>
  </el-form>
</div>
</template>

<script>
import startMeasures from '@/utils/measuresSo'
export default {
  data() {
    return {
      wiseIP: '192.168.58.200',
      measuresSoDatas: [],
      projectPhaseId: '',
      soLocationNumber: '',
      soItem: {},
      isSend: false,
      isMeasuring: false,
      currentSoLocationIndex: -1
    }
  },
  methods: {
    measures: function() {
      startMeasures(
        this.wiseIP,
        this.measuresSoDatas,
        this.soItem,
        this.currentDepth
      ).then(() => {
        this.isMeasuring = false
      })
      this.isMeasuring = true
    },
    clearMeasuresDatas: function() {
      this.measuresSoDatas = []
      this.isSend = false
    },
    uploadMeasuresDatas: function() {
      let measuresData = {
        projectId: this.$route.params.projectId,
        projectPhaseId: this.projectPhaseId,
        soLocationNumber: this.soLocationNumber,
        soItemId: this.soItem.id,
        soItemParameters: this.soItem.parameters,
        measureResult: this.measuresSoDatas
      }
      this.$store.dispatch('uploadMeasuresDatas', measuresData)
      this.isSend = true
    },
    getSOItem: function() {
      this.$store.dispatch('getSOItem', this.me.soItem.id).then(response => {
        this.soItem = response.data.data
      })
    },
    onChangeLocation: function() {
      let filterResult
      filterResult = this.project.soLocation.filter(item => {
        return item.number === this.soLocationNumber
      })
      this.currentSoLocationIndex = this.project.soLocation.indexOf(
        filterResult.shift()
      )
    }
  },
  computed: {
    project: function() {
      return this.$store.getters.currentProject
    },
    projectNumber: function() {
      return this.project.number
    },
    projectName: function() {
      return this.project.name
    },
    projectPhases: function() {
      return this.$store.getters.projectPhases
    },
    me: function() {
      return this.$store.getters.me
    },
    currentDepth: function() {
      return ~this.currentSoLocationIndex
        ? this.project.soLocation[this.currentSoLocationIndex].depth
        : 0
    }
  },
  mounted() {
    this.getSOItem()
  }
}
</script>
